class AppHeader extends Component {

  state = {
    gameSearched: [],
    DataisLoaded: false,
    searchText: null
  };


  handleInputChange = event => {
    // if (event.key === 'Enter') {
    //   alert(ele.value);
    // }
    this.props.onSearched(event.target.value);

    //   <Redirect to={"/browse"} />


    // console.log(this.props.searchTerm)
  }


  // fetchGamesBySearch = () => {
  //   let url = API_BASE_URL + "/game/search/" + this.props.title;
  //   // let url = API_BASE_URL + "/game/gameList";
  //   console.debug(url);
  //   fetch(
  //     url)
  //     .then((res) => res.json())
  //     .then((json) => {
  //       setGames(json);
  //       console.log(games)

  //     })
  // }
  // onChange={SearchData} value={search}
  render() {
    return (
      // <!-- ***** Header Area Start ***** -->
      <header className="header-area header-sticky">
        <div className="container">
          <div className="row">
            <div className="col-12">
              <nav className="main-nav">
                {/* <!-- ***** Logo Start ***** --> */}
                <a href="/" className="logo">
                  <img src="images/logo.png" alt="" />
                </a>
                {/* <!-- ***** Logo End ***** -->
                      <!-- ***** Search End ***** --> */}
                <div className="search-input">
                  <form id="search" action="/browse">

                    <input type="text" value={this.props.searchTerm} onChange={event => this.handleInputChange(event)} placeholder="Type Something" id='searchText' name="search"
                      onkeypress="handle" />
                    <i className="fa fa-search"></i>
                  </form>
                </div>
                {/* <!-- ***** Search End ***** --> */}

                {/* <!-- ***** Menu Start ***** --> */}
                {this.props.authenticated ? (
                  <ul className="nav">
                    <li>
                      <a onClick={this.props.onLogout}>Logout</a>
                    </li>
                    <li>
                      {
                        this.props.currentUser.imageUrl ? (
                          <NavLink to="/profile">{this.props.currentUser.name} <img src={this.props.currentUser.imageUrl} alt={""} srcset="" /></NavLink>
                        ) : (
                          <NavLink to="/profile">{this.props.currentUser.name} <img src={this.props.currentUser.name} alt={this.props.currentUser.name} srcset="" /></NavLink>
                        )
                      }

                    </li>

                  </ul>
                ) : (
                  <ul className="nav">
                    <li>
                      <NavLink to="/upload-game">Upload</NavLink>
                    </li>
                    <li>
                      <NavLink to="/browse">Browse</NavLink>
                    </li>
                    <li>
                      <NavLink to="/signup">Signup</NavLink>
                    </li>

                    <li>
                      <NavLink to="#">Profile <img src={profilepic} alt="" srcset="" /></NavLink>
                    </li>
                  </ul>
                )}

                <a className='menu-trigger'>
                  <span>Menu</span>
                </a>

                {/* <!-- ***** Menu End ***** --> */}
              </nav>
            </div>
          </div>
        </div>
      </header>
    );
  }
}

export default AppHeader;