import React, { Component, useEffect, useState } from 'react';
import { API_BASE_URL, React_Base_URL } from '../../Constants';

// import { fetchGamesByGenre } from '../../API/GameUtils';

import InfiniteLoader from 'react-infinite-loader';



const Browse = (props) => {

    // const params = new URLSearchParams(window.location.pathname);
    const queryParams = new URLSearchParams(window.location.search);

    const genre = queryParams.get('genre');
    const search = queryParams.get('search');


    // const [searchParams] = useSearchParams;
    const [content, setContent] = useState([]);
    const [browsedGames, setBrowsedGames] = useState([]);

    const [pageNo, setPageNo] = useState(0);



    const fetchGamesByGenre = () => {
        let url = API_BASE_URL + "/game/page/" + genre + "/" + "0" + "/" + "20";
        // let url = API_BASE_URL + "/game/gameList";

        fetch(
            url)
            .then((res) => res.json())
            .then((json) => {
                if (json.content != null) {
                    let games = [];
                    games = games.concat(browsedGames, json.content);

                    setBrowsedGames(games);
                    console.log(browsedGames)
                    setPageNo(pageNo + 1);
                }
            })
    }

    const fetchGamesBySearch = () => {
        let url = API_BASE_URL + "/game/search/" + search;
        // console.log(url)
        // let url = API_BASE_URL + "/game/gameList";

        fetch(
            url)
            .then((res) => res.json())
            .then((json) => {
                if (json != null) {
                    let games = [];
                    games = games.concat(browsedGames, json);

                    setBrowsedGames(games);
                    console.log(browsedGames)
                    setPageNo(pageNo + 1);
                }
            })
    }


    // const fetchGenreGames = () => {
    //     setContent(fetchGamesByGenre(genre));

    //     if (content != null) {
    //         let games = [];
    //         games = games.concat(browsedGames, content);

    //         setBrowsedGames(games);
    //         // console.log(browsedGames)
    //         setPageNo(pageNo + 1);
    //     }
    // }

    useEffect(() => {
        console.log(search);
        // fetchGenreGames();
        if (search != null) {
            fetchGamesBySearch();
        } else if (genre != null) {
            fetchGamesByGenre();
        }
        // console.log(search);

    }, [])



    function loadItems() {
        // fetchGenreGames();
        if (search != null) {
            fetchGamesBySearch();
        } else if (genre != null) {
            fetchGamesByGenre();
        }
    }

    function handleVisit() {
        loadItems()
    }

    function getItems() {
        let items = []
        for (var i = 0; i < 10; i++) {
            items.push({ name: 'An item' })
        }
        return items
    }

    function renderCards() {
        const { items } = this.state
        const cards = items.map((item, i) => {
            return (
                <div key={i}><h3>{item.name}</h3></div>
            )
        })
        return cards
    }

    return (
        <div class="container">
            <div class="row">
                <div class="col-lg-12">
                    <div class="page-content">

                        <div class="most-popular">
                            {
                                (genre != null) ?
                                    <div className="heading-section">
                                        <h4><em> <a href={React_Base_URL + "/browse?genre=" + genre}>{genre}</a></em> Games</h4>
                                    </div>
                                    : null
                            }

                            <div class="row">
                                <div class="col-lg-12">

                                    <div class="row">

                                        {
                                            browsedGames != [] ?
                                                browsedGames.map(game => (

                                                    <div class="col-lg-3 col-sm-6">
                                                        <div class="item">
                                                            <img src={game.cover} alt="" />
                                                            <h4>{game.gameTitle}<br /><span>{game.genreName}</span></h4>
                                                            <ul>
                                                                <li><i class="fa fa-star"></i> 4.8</li>
                                                                <li><i class="fa fa-download"></i> 2.3M</li>
                                                            </ul>
                                                        </div>
                                                    </div>

                                                )) : null
                                        }
                                        <InfiniteLoader onVisited={() => handleVisit()} />

                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
}
export default Browse;


