import React, { Component, useEffect, useState } from 'react';
import { API_BASE_URL, React_Base_URL } from '../../../Constants';
import { Link } from 'react-router-dom';



const GamesGrid = (props) => {

  const [games, setGames] = useState("");


  const fetchGamesByGenre = () => {
    let url = API_BASE_URL + "/game/page/" + props.genre + "/" + "0" + "/" + "8";
    // let url = API_BASE_URL + "/game/gameList";
    console.debug(url);
    fetch(
      url)
      .then((res) => res.json())
      .then((json) => {
        setGames(json);
        console.log(games)

      })
  }

  useEffect(() => {
    fetchGamesByGenre();
  }, [])



  return (
    <div className="most-popular">
      <div className="row">
        <div className="col-lg-12">
          <div className="heading-section">

            <div className='row '>
              <div className='col-lg-6'>
                <h4><em> <a href={React_Base_URL + "/browse?genre=" + props.genre}>{props.genre}</a></em> Games</h4>
              </div>
              <div className='col-lg-6 '>
                <h4> <Link to={{
                  pathname: "/preloaded",
                  state: { genre: props.genre }
                }}> <em>Pre</em></Link> Load</h4>
              </div>
            </div>
          </div>

          <div className="row">
            {
              games != [] ?
                games.content.map(game => (
                  <div className="col-lg-3 col-sm-6">
                    <div className="item">

                      <a href={React_Base_URL + "/gaming?id=" + game.id}> <img src={game.cover} alt="" /></a>
                      <h4>{game.gameTitle}<br /><span>Sandbox</span></h4>
                      <ul>
                        <li><i className="fa fa-star"></i> 4.8</li>
                        <li><i className="fa fa-download"></i> 2.3M</li>
                      </ul>
                    </div>
                  </div>
                )) : null
            }
          </div>
        </div>
      </div>
    </div>
  )
}

export default GamesGrid;

